"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import Image from "next/image";
import { supabase } from "@/lib/supabaseClient";

interface Country {
  id: number;
  name: string;
  code: string;
  risk_score: number;
  status_color: string;
}

export default function CrashWatch() {
  const [countries, setCountries] = useState<Country[]>([]);
  const [loading, setLoading] = useState(true);
  const router = useRouter();

  // fetch countries
  useEffect(() => {
    async function loadCountries() {
      setLoading(true);
      const { data, error } = await supabase
        .from("countries")
        .select("id, name, code, risk_score, status_color")
        .order("risk_score", { ascending: false });
      if (error) console.error("‚ùå Supabase fetch error:", error);
      else setCountries(data || []);
      setLoading(false);
    }

    loadCountries();

    // optional: auto-refresh every minute
    const timer = setInterval(loadCountries, 60 * 1000);
    return () => clearInterval(timer);
  }, []);

  if (loading)
    return (
      <div className="p-4 text-sm text-slate-500">Loading Crash Watch...</div>
    );

  return (
    <div className="p-3 rounded-lg border bg-white overflow-y-auto max-h-[600px]">
      <h2 className="text-sm uppercase font-semibold text-slate-500 mb-2">
        Crash Watch
      </h2>

      {countries.length === 0 ? (
        <div className="text-sm text-slate-500">No data available.</div>
      ) : (
        <ul className="space-y-2">
          {countries.map((c) => (
            <li
              key={c.id}
              onClick={() => router.push(`/intel/${c.code}`)}
              className="flex items-center justify-between p-2 rounded-md border hover:bg-slate-50 transition cursor-pointer"
            >
              <div className="flex items-center gap-2">
                {/* flag */}
                <Image
                  src={`https://flagcdn.com/24x18/${c.code.toLowerCase()}.png`}
                  alt={c.name}
                  width={24}
                  height={18}
                  unoptimized
                  className="border rounded-sm"
                />
                <span className="text-sm font-medium text-slate-800">
                  {c.name}
                </span>
              </div>

              <div
                className={`text-sm font-semibold ${
                  c.status_color === "red"
                    ? "text-red-600"
                    : c.status_color === "amber"
                    ? "text-amber-500"
                    : "text-blue-600"
                }`}
              >
                {c.risk_score}
              </div>
            </li>
          ))}
        </ul>
      )}
    </div>
  );
}
