"use client";

import { useEffect, useState } from "react";
import NewsFeed from "@/components/NewsFeed";
import GlobeView from "@/components/GlobeView";
import CrashWatch from "@/components/CrashWatch";
import PredictiveTimeline from "@/components/PredictiveTimeline";

export default function Home() {
  const [asset, setAsset] = useState("GOLD");
  const [region, setRegion] = useState("ALL");
  const [topic, setTopic] = useState("global");
  const [dailySummary, setDailySummary] = useState<string>("");

  /* ============= Fetch AI Daily Summary ============= */
  useEffect(() => {
    const fetchSummary = async () => {
      try {
        const res = await fetch("/api/summary?topic=global");
        const data = await res.json();
        setDailySummary(data.summary || "");
      } catch {
        setDailySummary(
          "Markets await clarity amid mixed signals from policymakers and emerging trends across energy, inflation, and crypto sectors."
        );
      }
    };
    fetchSummary();
  }, []);

  /* ============= UI LAYOUT ============= */
  return (
    <main className="min-h-screen bg-slate-50 text-slate-800">
      {/* ---------- HEADER ---------- */}
      <div className="sticky top-0 z-30 bg-slate-50/90 backdrop-blur border-b shadow-sm">
        <div className="max-w-7xl mx-auto px-6 pt-6 pb-4 text-center">
          <h1 className="text-3xl font-bold text-slate-900 tracking-tight">
            eX Intelligence Dashboard
          </h1>
          <p className="text-sm text-slate-600">
            Organize everything. Predict anything. Monitor the pulse of global finance.
          </p>
        </div>
      </div>

      {/* ---------- MAIN GRID ---------- */}
      <div className="max-w-7xl mx-auto px-4 py-8 flex flex-col md:flex-row gap-6">
        {/* === LEFT PANEL: CRASH WATCH === */}
        <div className="md:w-1/3 space-y-4">
          <CrashWatch />
        </div>

        {/* === MIDDLE PANEL: GLOBE + REGION GALLERY === */}
        <div className="md:w-1/3 space-y-4">
          <div className="rounded-xl border bg-white shadow-sm p-4">
            <GlobeView width={240} height={210} title="Global Overview" />
          </div>

          {/* REGION GALLERY */}
          <div className="rounded-xl border bg-white shadow-sm p-4">
            <h2 className="text-xs uppercase tracking-wider text-slate-500 mb-2 flex items-center gap-1">
              üåç Region Gallery
            </h2>
            <div className="grid grid-cols-2 gap-2">
              {["ALL", "USA", "EU", "ASIA", "AFRICA", "LATAM"].map((r) => (
                <button
                  key={r}
                  onClick={() => setRegion(r)}
                  className={`px-3 py-2 text-sm border rounded-lg ${
                    region === r
                      ? "bg-blue-600 text-white border-blue-600"
                      : "bg-white text-slate-700 border-slate-300 hover:bg-slate-50"
                  }`}
                >
                  {r}
                </button>
              ))}
            </div>
          </div>

          {/* LIVE INTELLIGENCE FEED */}
          <div className="rounded-xl border bg-white shadow-sm p-4">
            <h2 className="text-xs uppercase tracking-wider text-slate-500 mb-2">
              üß† Live Intelligence Feed
            </h2>
            <NewsFeed asset={asset} topic={topic} region={region} />
          </div>
        </div>

        {/* === RIGHT PANEL: PREDICTIVE TIMELINE === */}
        <div className="md:w-1/3 space-y-4">
          <PredictiveTimeline asset={asset} region={region} />
        </div>
      </div>
    </main>
  );
}
